.trecker-card {
  background: #e15241;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  gap: 15px;

  width: 262px;
  height: 262px;
  min-width: 262px;
  min-height: 262px;

  border-radius: 12px;
  position: relative;
}

.trecker-card {
  &__btn {
    border-radius: 100%;
    width: 37px;
    height: 37px;

    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__btn--close {
    width: 25px;
    height: 25px;
    position: absolute;
    top: 10px;
    right: 10px;
  }
}
